import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { motion } from "framer-motion"
import { GitHub, Email, Favorite } from "@mui/icons-material"

const FooterContainer = styled(motion.footer)`
  background: #31314d;
  color: white;
  padding: 60px 20px 30px;
  position: relative;
  overflow: hidden;
`

const ContentWrapper = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
`

const FooterSection = styled(motion.div)`
  h3 {
    color: #00cec9;
    margin-bottom: 20px;
    font-size: 1.5rem;
    position: relative;
    display: inline-block;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 50px;
      height: 3px;
      background: #6c5ce7;
    }
  }

  p {
    line-height: 1.6;
    margin-bottom: 15px;
  }
`

const FooterLink = styled(motion(Link))`
  color: white;
  text-decoration: none;
  display: block;
  margin-bottom: 10px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;

  &:hover {
    color: #00cec9;
    transform: translateX(5px);
  }
`

const ExternalLink = styled(motion.a)`
  color: white;
  text-decoration: none;
  display: block;
  margin-bottom: 10px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;

  &:hover {
    color: #00cec9;
    transform: translateX(5px);
  }
`

const SocialLinks = styled.div`
  display: flex;
  gap: 20px;
  margin-top: 20px;
`

const SocialIcon = styled(motion.a)`
  color: white;
  font-size: 24px;
  transition: all 0.3s ease;

  &:hover {
    color: #00cec9;
    transform: scale(1.2);
  }
`

const Copyright = styled(motion.div)`
  text-align: center;
  margin-top: 60px;
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
`

const sectionVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      ease: "easeOut",
    },
  },
}

const Footer = () => {
  return (
    <FooterContainer
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      viewport={{ once: true, margin: "-100px" }}
    >
      <ContentWrapper>
        <FooterSection
          variants={sectionVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
        >
          <h3>ПсихоЛогика</h3>
          <p>Ваш проводник в мире гармоничных отношений и личностного роста.</p>
          <SocialLinks>
            <SocialIcon
              href="https://github.com/vusbeyt"
              target="_blank"
              rel="noopener noreferrer"
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.9 }}
            >
              <GitHub fontSize="inherit" />
            </SocialIcon>
          </SocialLinks>
        </FooterSection>

        <FooterSection
          variants={sectionVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
          transition={{ delay: 0.2 }}
        >
          <h3>Навигация</h3>
          <FooterLink to="/" whileHover={{ x: 5 }}>
            Главная
          </FooterLink>
          <FooterLink to="/status" whileHover={{ x: 5 }}>
            Выбор статуса
          </FooterLink>
        </FooterSection>

        <FooterSection
          variants={sectionVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
          transition={{ delay: 0.4 }}
        >
          <h3>Контакты</h3>
          <ExternalLink
            href="mailto:h.frydrykh@sava.team"
            whileHover={{ x: 5 }}
          >
            <Email /> h.frydrykh@sava.team
          </ExternalLink>
        </FooterSection>
      </ContentWrapper>

      <Copyright
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        transition={{ delay: 0.6 }}
      >
        <Favorite style={{ color: "#ff6b81", fontSize: "20px" }} />
        <span>
          © {new Date().getFullYear()} ПсихоЛогика. Все права защищены.
        </span>
      </Copyright>
    </FooterContainer>
  )
}

export default Footer
